import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { withRouter, Link } from "react-router-dom";
import "./CalendarForm.css";
import "../common/Button.css";
import circleIMG from "../../images/circle_svg.svg";
import { Body } from "../../pages/MainPage";
import Button from "@material-ui/core/Button";
import EditIcon from '@material-ui/icons/Edit';
import { createMuiTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";
var Sex;
var Control;
var Mood;
var Condition;
var Memo;

var Date_send = 0;

function CalendarForm_Today(props) {
  const rawDate = new Date(props.match.params.date);
  const showDate = rawDate.getDate();
  const dispatch = useDispatch();
  Date_send = props.match.params.date;


  const onLoadHandler = (event) => {
    if (Body.isSex) {
      if (Body.isProtection) {
        Sex = " ,  !";
      } else {
        Sex = " ,   !";
      }
    } else {
      Sex = "  !";
    }

    if (Body.isControl) {
      Control = " !";
    } else {
      Control = "  !";
    }

    if (Body.dateMood === 1)
      Mood =
        props.match.params.name +
        ",              !";
    else if (Body.dateMood === 2)
      Mood =
        props.match.params.name +
        ",    " +
        props.match.params.name +
        "       !";
    else if (Body.dateMood === 3)
      Mood =
        props.match.params.name +
        ",   ! " +
        props.match.params.name +
        "     !";
    else if (Body.dateMood === 4)
      Mood =
        props.match.params.name +
        ",     !  " +
        props.match.params.name +
        "     !";
    else if (Body.dateMood === 5)
      Mood =
        props.match.params.name +
        ",      ! " +
        props.match.params.name +
        "   !";
    else if (Body.dateMood === 6)
      Mood =
        props.match.params.name +
        ",     !        " +
        props.match.parmas.name +
        " ~ !";
    else if (Body.dateMood === 7)
      Mood =
        props.match.params.name +
        ",        ,     ?";
    else if (Body.dateMood === 8)
      Mood =
        props.match.params.name +
        ",     !      ,    !";
    else if (Body.dateMood === 9)
      Mood =
        props.match.params.name +
        ",    !              !";
    else if (Body.dateMood === 10)
      Mood =
        props.match.params.name +
        ",    !     ,         !";
    else if (Body.dateMood === 11)
      Mood =
        props.match.params.name +
        ",     ,   !         ?";
    else if (Body.dateMood === 12)
      Mood =
        props.match.params.name +
        ",    ! " +
        props.match.params.name +
        "    ,    ~";
    else if (Body.dateMood === 13)
      Mood =
        props.match.params.name +
        ",    " +
        props.match.params.name +
        "      ,  ,  ,   !";
    else if (Body.dateMood === 14)
      Mood =
        props.match.params.name +
        ",    " +
        props.match.params.name +
        " !        ? !";
    else
      Mood = props.match.params.name + ",    !";

    if (Body.dateCondition1 === 1) Condition = " ";
    else if (Body.dateCondition1 === 2)
      Condition=""
    else if (Body.dateCondition1 === 3) Condition = " ";
    else if (Body.dateCondition1 === 4) Condition = " ";
    else if (Body.dateCondition1 === 5) Condition = "  ";
    else if (Body.dateCondition1 === 6) Condition = " ";
    else if (Body.dateCondition1 === 7) Condition = "";
    else if (Body.dateCondition1 === 8) Condition = "";
    else if (Body.dateCondition1 === 9)
      Condition = "";
    else if (Body.dateCondition1 === 10) Condition = "";
    else if (Body.dateCondition1 === 11)
      Condition = " ";
    else if (Body.dateCondition1 === 12) Condition = "";
    else
      Condition =
        props.match.params.name + ",    !";

    if (Body.dateMemo) Memo = " : " + Body.dateMemo;
    else Memo = props.match.params.name + ",   !";
  };


  const onClickHandler = (event) => {
    //event.preventDefault();
    console.log("   ", props.match.params.date);
    props.history.push("/calendar/" + props.match.params.name);
  };

  const onEditHandler = (event) => {
    console.log("   ", props.match.params.date);
    props.history.push("/calendar_detail/"+props.match.params.name+"/"+props.match.params.date);
  };
  const theme = createMuiTheme({
    palette: {
      primary: {
        main: "#28293c",
      },
      secondary: {
        main: "#c8bbb3",
      },
    },
  });
  return (
    <div style={{ margin: "auto", width: "80%" }}>
      {onLoadHandler()}
      <div style={{ position: "relative" }}>
        <img
          src={circleIMG}
          onClick={onClickHandler}
          style={{
            verticalAlign: "middle",
            position: "relative",
            zIndex: "2",
            display: "block",
            marginTop: "30px",
            marginLeft: "auto",
            marginRight: "auto",
            width: "60px",
            height: "60px",
            padding: "0",
          }}
        />
        <span
          onClick={onClickHandler}
          style={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate( -50%, -50% )",
            fontSize: "30px",
            zIndex: "3",
            color: "white",
          }}
        >
          {showDate}
        </span>
      </div>
      <div
        style={{
          lineHeight: "2",
          fontSize: "17px",
          padding: "20px",
          paddingTop: "40px",
          position: "relative",
          top: "-40px",
          zIndex: "1",
          borderRadius: "30px",
          boxShadow: "0 3px 6px 0 rgba(0, 0, 0, 0.16)",
          backgroudnColor: "#f9f7f7",
          backgroundColor: "#eeeeee",
          marginBottom: "100px",
        }}
      >
        <div>
          {Sex}
          <br />
          {Control}
          <br />
          {Mood}
          <br />
          {Condition}
          <br />
          {Memo}
        </div>
        <div style={{textAlign:"center"}}>
          <br/>
          <ThemeProvider theme={theme}>
          <Button
                variant="contained"
                color="primary"
                onClick={onEditHandler}
                size="small"
                startIcon={<EditIcon />}
                style={{
                  marginTop: "1rem",
                  width: "40%",
                  fontFamily: "IBMPlexSansKR-Regular",
                }}
              ></Button>
          </ThemeProvider>        </div>
      </div>
    </div>
  );
}

export default withRouter(CalendarForm_Today);
export { Date_send };
