import axios from "axios";
import { REGISTER_USER, LOGIN_USER, LOGOUT_USER } from "./types";
axios.defaults.withCredentials=true;

export function loginUser(dataToSubmit) {
  const request = axios
    .post("http://3.34.183.92:4000/api/auth/login", dataToSubmit, {withCredentials:true})
    .then((response) => response.data);

  return {
    type: LOGIN_USER,
    payload: request,
  };
}

export function registerUser(dataTosubmit) {
  //   request 
  const request = axios
    .post("http://3.34.183.92:4000/api/auth/register", dataTosubmit)
    .then((response) => response.data);

  //   
  return {
    type: REGISTER_USER,
    payload: request,
  };
}

export function logoutUser() {
  const request = axios
    .get("http://3.34.183.92:4000/api/auth/logout", {withCredentials:true})
    .then((response) => response.data);

  return {
    type: LOGOUT_USER,
    payload: request,
  };
}


