import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { withRouter } from "react-router-dom";
import { inputDate } from "../../_actions/date_action";
import "./CalendarForm.css";
import "../common/Button.css";
import circleIMG from "../../images/circle_svg.svg";
import { FiChevronDown } from "react-icons/fi";
import Button from "@material-ui/core/Button";
import { createMuiTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";
import TextField from "@material-ui/core/TextField";
import Checkbox from "@material-ui/core/Checkbox";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControl from "@material-ui/core/FormControl";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormLabel from "@material-ui/core/FormLabel";
import SaveIcon from "@material-ui/icons/Save";
import Favorite from "@material-ui/icons/Favorite";
import FavoriteBorder from "@material-ui/icons/FavoriteBorder";
import Alert from "@material-ui/lab/Alert";
import IconButton from "@material-ui/core/IconButton";
import Collapse from "@material-ui/core/Collapse";
import CloseIcon from "@material-ui/icons/Close";
//import callDay from "./CalendarForm";
//import clickedday from './CalendarForm';

function CalendarForm_Detail(props) {
  var count = 0;

  const dispatch = useDispatch();
  const rawDate = new Date(props.match.params.date);
  const showDate = rawDate.getDate();
  const date = props.match.params.date;

  const [cycleStart, setCycleStart] = useState("");
  const [cycleEnd, setCycleEnd] = useState("");
  const [isProtection, setProtection] = useState("");
  const [isSex, setSex] = useState("");
  const [isControl, setControl] = useState("");
  const [dateMood, setMood] = useState("");
  const [dateCondition, setCondition] = useState([]);
  const [dateMemo, setMemo] = useState("");

  const Start = date;
  const End = date;

  const onStartHandler = (event) => {
    setCycleStart(event.currentTarget.value);
    console.log(event.currentTarget.value);
  };
  const onEndHandler = (event) => {
    setCycleEnd(event.currentTarget.value);
    console.log(event.currentTarget.value);
  };
  const onControlHandler = (event) => {
    setControl(event.currentTarget.value);
  };
  const onSexHandler = (event) => {
    setSex(event.currentTarget.value);
  };
  const onProtectionHandler = (event) => {
    setProtection(event.currentTarget.value);
  };
  const onMoodHandler = (event) => {
    setMood(event.currentTarget.value);
  };
  const onConditionHandler = (event) => {
    var condi = document.getElementsByName("condiCheck");
    var cnt = 0;
    for (var i = 0; i < 12; i++) {
      if (condi[i].checked) {
        cnt += 1;
        if (cnt > 3) {
          document.getElementById("condiAlert").style.display = "block";
          return;
        } else {
          document.getElementById("condiAlert").style.display = "none";
          setCondition(event.currentTarget.value);
        }
      }
    }
  };
  const onMemoHandler = (event) => {
    setMemo(event.currentTarget.value);
  };

  const moodDisplay = () => {
    if (document.getElementById("mood").style.display === "none") {
      document.getElementById("mood").style.display = "block";
    } else {
      document.getElementById("mood").style.display = "none";
    }
  };

  const condiDisplay = () => {
    if (document.getElementById("condition").style.display === "none") {
      document.getElementById("condition").style.display = "block";
    } else {
      document.getElementById("condition").style.display = "none";
    }
  };
  const condiCheck = () => {
    var condi = document.getElementsByName("condiCheck");
    var cnt = 0;
    for (var i = 0; i < 12; i++) {
      if (condi[i].checked) {
        cnt += 1;
        if (cnt > 3) {
          document.getElementById("condiAlert").style.display = "block";
          return;
        } else {
          document.getElementById("condiAlert").style.display = "none";
        }
      }
    }
  };

  const contraDisplay = () => {
    if (document.getElementById("clickcontra").value === "1") {
      document.getElementById("contraception").style.display = "block";
    } else {
      document.getElementById("contraception").style.display = "none";
    }
  };

  const onClickHandler = (event) => {
    //event.preventDefault();
    console.log("   ", props.match.params.date);
    props.history.push("/calendar/" + props.match.params.name);
  };

  const onSubmitHandler = (event) => {
    event.preventDefault();
    console.log("");
    console.log("", cycleStart);
    console.log("", cycleEnd);
    console.log(" ", isControl);
    console.log(" ", isSex);
    console.log(" ", isProtection);
    console.log("", dateCondition);
    console.log("", dateMood);
    console.log("", dateMemo);
    console.log("");
    console.log(date);
    //   null    
    var cycle_Start;
    var cycle_End;
    var is_Control;
    var is_Sex;
    var is_Protection;
    var date_Mood;
    var date_Condition1 = "";
    var date_Condition2 = "";
    var date_Condition3 = "";
    var date_Memo;
    var date_Condition=[];
    if (
      cycleStart === "" &&
      cycleEnd === "" &&
      isControl === "" &&
      isSex === "" &&
      isProtection === "" &&
      dateMood === "" &&
      dateCondition[0] === undefined &&
      dateCondition[1] === undefined &&
      dateCondition[2] === undefined &&
      dateMemo === ""
    ) {
      console.log(" ");
      document.getElementById("none").style.display = "block";
      return;
    }

    var condi = document.getElementsByName("condiCheck");
    var cnt = 0;
    for (var i = 0; i < 12; i++) {
      if (condi[i].checked) {
        cnt += 1;
        if (cnt > 3) {
          console.log("      .");
          document.getElementById("condiAlert").style.display = "block";
          return;
        }
        else {
          console.log(condi[i].value);
          date_Condition.push(condi[i].value);
          var count=0;
          
        }
      }
    }
    console.log(date_Condition);
    
    if (cycleStart === "") {
      cycle_Start = null;
    } else {
      cycle_Start = cycleStart;
    }
    if (cycleEnd === "") {
      cycle_End = null;
    } else {
      cycle_End = cycleEnd;
    }
    if (isControl === "") {
      // is_Control = null;
    } else {
      is_Control = isControl;
    }
    if (isSex === "") {
      is_Sex = null;
    } else {
      is_Sex = isSex;
    }
    if (isProtection === "") {
      is_Protection = null;
    } else {
      is_Protection = isProtection;
    }
    if (dateMood === "") {
      date_Mood = null;
    } else {
      date_Mood = dateMood;
    }

    if (dateCondition === []) {
      date_Condition1 = null;
      date_Condition2 = null;
      date_Condition3 = null;
    } else {
      date_Condition1 = date_Condition[0];
      date_Condition2 = date_Condition[1];
      date_Condition3 = date_Condition[2];
    }

    
    if (date_Condition[0]===undefined){
      date_Condition1 = null;
    }
    if (date_Condition[1]===undefined){
      date_Condition2 = null;
    }
    if (date_Condition[2]===undefined){
      date_Condition3 = null;
    }

    if (dateMemo === "") {
      date_Memo = null;
    } else {
      date_Memo = dateMemo;
    }

    let body = {
      date: date,
      cycleStart: cycle_Start,
      cycleEnd: cycle_End,
      isControl: is_Control,
      isSex: is_Sex,
      isProtection: is_Protection,
      dateMood: date_Mood,
      dateCondition1: date_Condition1,
      dateCondition2: date_Condition2,
      dateCondition3: date_Condition3,
      dateMemo: date_Memo,
    };
    console.log(body);
    
    dispatch(inputDate(body)).then((response) => {
      console.log("     : ", response.payload);

      if (response.payload.completed === true) {
        console.log(date, "   .");
        document.getElementById("success").style.display = "block";
        //alert("  .");
      } else if (
        response.payload === "     ."
      ) {
        console.log("    ", response.payload);
        document.getElementById("needEnd").style.display = "block";
        //alert("     .");
        return;
      } else {
        console.log(" ");
        document.getElementById("error").style.display = "block";
        //alert(" ");
        return;
      }
    });
  };
  const [open, setOpen] = React.useState(true);

  const theme = createMuiTheme({
    palette: {
      primary: {
        main: "#28293c",
      },
      secondary: {
        main: "#c8bbb3",
      },
    },
  });

  return (
    <div style={{ position: "relative", paddingBottom: "1px" }}>
      <Collapse
        in={open}
        id="none"
        style={{
          position: "fixed",
          width: "100%",
          display: "none",
          zIndex: "50",
          top: "0",
        }}
      >
        <Alert
          severity="error"
          style={{ margin: "5px" }}
          action={
            <IconButton
              aria-label="close"
              color="inherit"
              size="small"
              onClick={() => {
                document.getElementById("none").style.display = "none";
              }}
            >
              <CloseIcon fontSize="inherit" />
            </IconButton>
          }
        >
              .   .
        </Alert>
      </Collapse>
      <Collapse
        in={open}
        id="success"
        style={{
          position: "fixed",
          width: "100%",
          display: "none",
          zIndex: "50",
          top: "0",
        }}
      >
        <Alert
          style={{ margin: "5px" }}
          action={
            <IconButton
              aria-label="close"
              color="inherit"
              size="small"
              onClick={() => {
                document.getElementById("success").style.display = "none";
              }}
            >
              <CloseIcon fontSize="inherit" />
            </IconButton>
          }
        >
             .
        </Alert>
      </Collapse>
      <Collapse
        in={open}
        id="needEnd"
        style={{
          position: "fixed",
          width: "100%",
          display: "none",
          zIndex: "50",
          top: "0",
        }}
      >
        <Alert
          severity="error"
          style={{ margin: "5px" }}
          action={
            <IconButton
              aria-label="close"
              color="inherit"
              size="small"
              onClick={() => {
                document.getElementById("needEnd").style.display = "none";
              }}
            >
              <CloseIcon fontSize="inherit" />
            </IconButton>
          }
        >
               .
        </Alert>
      </Collapse>
      <Collapse
        in={open}
        id="error"
        style={{
          position: "fixed",
          width: "100%",
          display: "none",
          zIndex: "50",
          top: "0",
        }}
      >
        <Alert
          severity="error"
          style={{ margin: "5px" }}
          action={
            <IconButton
              aria-label="close"
              color="inherit"
              size="small"
              onClick={() => {
                document.getElementById("error").style.display = "none";
              }}
            >
              <CloseIcon fontSize="inherit" />
            </IconButton>
          }
        >
           .    .
        </Alert>
      </Collapse>
      <Collapse
        in={open}
        id="condiAlert"
        style={{
          position: "fixed",
          width: "100%",
          display: "none",
          zIndex: "50",
          top: "0",
        }}
      >
        <Alert
          severity="error"
          style={{ margin: "5px" }}
          action={
            <IconButton
              aria-label="close"
              color="inherit"
              size="small"
              onClick={() => {
                document.getElementById("condiAlert").style.display = "none";
              }}
            >
              <CloseIcon fontSize="inherit" />
            </IconButton>
          }
        >
                .
        </Alert>
      </Collapse>
      <div style={{ margin: "auto", width: "80%" }}>
        <div style={{ position: "relative" }}>
          <img
            src={circleIMG}
            onClick={onClickHandler}
            style={{
              verticalAlign: "middle",
              position: "relative",
              zIndex: "2",
              display: "block",
              marginTop: "30px",
              marginLeft: "auto",
              marginRight: "auto",
              width: "60px",
              height: "60px",
              padding: "0",
            }}
          />
          <span
            onClick={onClickHandler}
            style={{
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate( -50%, -50% )",
              fontSize: "30px",
              zIndex: "3",
              color: "white",
            }}
          >
            {showDate}
          </span>
        </div>
        <div
          style={{
            padding: "20px",
            paddingTop: "40px",
            position: "relative",
            top: "-40px",
            zIndex: "1",
            borderRadius: "30px",
            boxShadow: "0 3px 6px 0 rgba(0, 0, 0, 0.16)",
            backgroudnColor: "#f9f7f7",
            backgroundColor: "#eeeeee",
            marginBottom: "100px",
          }}
        >
          <form
            style={{ fontSize: "17px", lineHeight: "2" }}
            onSubmit={onSubmitHandler}
          >
            <ThemeProvider theme={theme}>
              <div>
                <FormControl component="fieldset">
                  <FormLabel
                    component="legend"
                    style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                  >
                     
                  </FormLabel>
                  <RadioGroup
                    row
                    aria-label="cycle"
                    name="cycle"
                    defaultValue="top"
                  >
                    <FormControlLabel
                      value="1"
                      onChange={onStartHandler}
                      control={<Radio color="primary" />}
                      label=" "
                      style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                    />
                    <FormControlLabel
                      value="2"
                      onChange={onEndHandler}
                      control={<Radio color="primary" />}
                      label=" "
                      style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                    />
                  </RadioGroup>
                </FormControl>
              </div>
              <div>
                <FormControl component="fieldset">
                  <FormLabel
                    component="legend"
                    style={{
                      fontFamily: "IBMPlexSansKR-Regular",
                      marginTop: "1rem",
                    }}
                  >
                      
                  </FormLabel>
                  <RadioGroup
                    row
                    aria-label="control"
                    name="control"
                    defaultValue="top"
                    onChange={onControlHandler}
                  >
                    <FormControlLabel
                      value="1"
                      name="isControl"
                      control={<Radio color="primary" />}
                      label=" "
                      style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                    />
                  </RadioGroup>
                </FormControl>
              </div>
              <div>
                <FormControl component="fieldset">
                  <FormLabel
                    component="legend"
                    style={{
                      fontFamily: "IBMPlexSansKR-Regular",
                      marginTop: "1rem",
                    }}
                  >
                     
                  </FormLabel>
                  <RadioGroup
                    row
                    aria-label="sex"
                    name="sex"
                    defaultValue="top"
                    onChange={onSexHandler}
                  >
                    <FormControlLabel
                      value="1"
                      name="isSex"
                      control={
                        <Radio
                          color="primary"
                          id="clickcontra"
                          icon={<FavoriteBorder />}
                          checkedIcon={<Favorite />}
                          onClick={contraDisplay}
                        />
                      }
                      label=" "
                      style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                    />
                  </RadioGroup>
                </FormControl>

                <div
                  id="contraception"
                  style={{ marginLeft: "20px", display: "none" }}
                >
                  <FormControl component="fieldset">
                    <FormLabel
                      component="legend"
                      style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                    >
                       
                    </FormLabel>
                    <RadioGroup
                      row
                      aria-label="protection"
                      name="protiection"
                      defaultValue="top"
                      onChange={onProtectionHandler}
                    >
                      <FormControlLabel
                        value="1"
                        control={<Radio size="small" color="primary" />}
                        label=" "
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="0"
                        control={<Radio size="small" color="primary" />}
                        label=" "
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                    </RadioGroup>
                  </FormControl>
                </div>
              </div>
              <div>
                <FormControl component="fieldset">
                  <FormLabel
                    component="legend"
                    onClick={condiDisplay}
                    style={{
                      fontFamily: "IBMPlexSansKR-Regular",
                      marginTop: "1rem",
                    }}
                  >
                      
                    <FiChevronDown />
                  </FormLabel>
                  <div
                    id="condition"
                    style={{ marginLeft: "20px", display: "none" }}
                  >
                    <Checkbox
                      name="condiCheck"
                      value={1}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                     
                    <Checkbox
                      name="condiCheck"
                      value={2}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                    
                    <Checkbox
                      name="condiCheck"
                      value={3}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                     
                    <Checkbox
                      name="condiCheck"
                      value={4}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                     
                    <Checkbox
                      name="condiCheck"
                      value={5}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                      
                    <Checkbox
                      name="condiCheck"
                      value={6}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                     
                    <Checkbox
                      name="condiCheck"
                      value={7}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                    
                    <Checkbox
                      name="condiCheck"
                      value={8}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                    
                    <Checkbox
                      name="condiCheck"
                      value={9}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                    
                    <Checkbox
                      name="condiCheck"
                      value={10}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                    
                    <Checkbox
                      name="condiCheck"
                      value={11}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                     
                    <Checkbox
                      name="condiCheck"
                      value={12}
                      color="primary"
                      size="small"
                      style={{ padding: "0", marginRight: "10px" }}
                      onClick={condiCheck}
                      onChange={onConditionHandler}
                    />
                    
                  </div>
                </FormControl>
              </div>

              <div>
                <FormControl component="fieldset">
                  <FormLabel
                    component="legend"
                    onClick={moodDisplay}
                    style={{
                      fontFamily: "IBMPlexSansKR-Regular",
                      marginTop: "1rem",
                    }}
                  >
                     <FiChevronDown />
                  </FormLabel>
                  <div
                    id="mood"
                    style={{ marginLeft: "20px", display: "none" }}
                  >
                    <RadioGroup
                      row
                      aria-label="mood"
                      name="mood"
                      defaultValue="top"
                      onChange={onMoodHandler}
                    >
                      <FormControlLabel
                        value="1"
                        control={<Radio size="small" color="primary" />}
                        label=" "
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="2"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="3"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="4"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="5"
                        control={<Radio size="small" color="primary" />}
                        label=" "
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="6"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="7"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="8"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="9"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="10"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="11"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="12"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="13"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                      <FormControlLabel
                        value="14"
                        control={<Radio size="small" color="primary" />}
                        label=""
                        style={{ fontFamily: "IBMPlexSansKR-Regular" }}
                      />
                    </RadioGroup>
                  </div>
                </FormControl>
              </div>
              <TextField
                label=""
                value={dateMemo}
                onChange={onMemoHandler}
                placeholder="  "
                style={{
                  width: "100%",
                  marginTop: "1rem",
                  marginBottom: "2rem",
                }}
                multiline
              />
              <div style={{ textAlign: "center" }}>
                <Button
                  variant="contained"
                  type="submit"
                  color="primary"
                  size="small"
                  startIcon={<SaveIcon />}
                  style={{
                    marginTop: "1rem",
                    width: "40%",
                    fontFamily: "IBMPlexSansKR-Regular",
                  }}
                >
                  
                </Button>
              </div>
            </ThemeProvider>
          </form>
        </div>
      </div>
    </div>
  );
}

export default withRouter(CalendarForm_Detail);

/*
<Button
              variant="raised"
              type="reset"
              color="secondary"
              style={{
                marginTop: "1rem",
                width: "40%",
                fontFamily: "IBMPlexSansKR-Regular",
              }}
            >
              
            </Button>
*/
